body, h1,h2,h3,h4,h5,h6 {
    font-family: "Montserrat", sans-serif
}

.container {
    max-width: 750px
}

.title {
   font-size: 2.5em;
   font-weight: bolder;
   color: #708090;
}

.w3-row-padding img {
    margin-bottom: 12px}

/
.w3-sidebar {
    width: 200px;
    background: #222;
}


.imageContainer {
  position: relative;
  text-align: center;
  color: white;
}

.centered {
  position: absolute;
  top: 20%;
  left: 10%;
  transform: translate(0%, -20%);
  font-size: 3em;
  font-weight: 900;
  opacity: 0.8;
  color: white;
  text-shadow: 2px 2px 6px gray;
}

#main {margin-left: 200px}


@media only screen and (max-width: 600px) {#main {margin-left: 0}}
